<template>
<div id="home">
  <sidebar @popup="popup"></sidebar>
  <div class="content">
    <h1 :class="{slid: state !== 0}">Kenny Tang, Developer</h1>
    <br/>
    <a @click="setState(1)">About Me</a>
    <transition name="scale">
      <div v-if="state === 1" class="about">
        <img class="portrait" alt="Portrait" :src="portrait">
        <p>Hello! I am Kenny Tang! I'm an avid developer. I am currently attending college at the Univeristy of Texas at Austin as a Freshman.<br/>
          I am majoring in Electrical and Computer Engineering. <br/>
          I have one year of experience in full stack development using technologies such as Node.js, Express, and Vue.js.
        </p>
      </div>
    </transition>
    <br/>
    <a @click="setState(2)">Projects</a>
    <transition name="scale">
      <div v-if="state === 2" class="about">
        <p v-if="error">{{error}}</p>
        <ul v-else class="projects">
          <li v-for="project in projects" :key="project.name"><a :href="`https://github.com/kftang/${project.name}`">{{project.name}}</a><br/></li>
        </ul>
      </div>
    </transition>
    <br/>
    <!-- <a @click="setState(3)">Resume</a>
    <transition name="scale">
      <div v-if="state === 3" class="resume">
        <h4>Academics</h4>
        <p>Round Rock High School</p>
        <p>The Univeristy of Texas at Austin</p>
        <h4>Work Experience</h4>
        <p>Ixia</p>
        <p>Linda Liu CPA Corp.</p>
      </div>
    </transition> -->
  </div>
</div>
</template>

<script>
import Sidebar from '@/components/Sidebar';

const portrait = require('@/assets/portrait.jpg');


export default {
  name: 'home',
  components: {
    Sidebar,
  },
  data() {
    return {
      portrait,
      state: 0,
      projects: [],
      error: '',
    };
  },
  created() {
    fetch('https://api.github.com/users/kftang/repos')
      .then((res) => {
        if (res.status !== 200) {
          throw res.status;
        }
        return res.json();
      })
      .then((repos) => {
        // const findCommits = [];
        repos.forEach((repo) => {
          const project = {};
          if (repo.fork) {
            return;
          }
          project.name = repo.name;
          project.stars = repo.stargazers_count;
          project.language = repo.language;
          this.projects.push(project);
        });
      })
      .catch((err) => {
        if (err === 403) {
          this.error = 'Could not load repos.';
        }
      });
  },
  methods: {
    popup(src) {
      this.src = src;
    },
    setState(state) {
      if (this.state === state) {
        this.state = 0;
      } else {
        this.state = state;
      }
    },
  },
};
</script>


<style scoped>
@import url('https://fonts.googleapis.com/css?family=Montserrat:200');
@media screen and (orientation: portrait) {
  @keyframes scale-in {
    from { font-size: 0em; }
    to { font-size: 3em; }
  }
  div#home {
    flex-direction: column-reverse;
  }
  div#home div.content > h1 {
    font-size: 6em;
  }
  div#home div.content > a {
    font-size: 8em;
  }
  div#home div.content > div {
    font-size: 3em;
  }
}
@media screen and (orientation: landscape) {
  @keyframes scale-in {
    from { font-size: 0em; }
    to { font-size: 1em; }
  }
}
@keyframes slide-in {
  from { margin: 2em 1em 1em 1em; }
  to { margin: 0 1em; }
}
@keyframes slide-out {
  from { margin: 0 1em; }
  to { margin: 2em 1em 1em 1em; }
}
div.content > h1:not(.slid) {
  animation: slide-out .5s backwards;
  animation-delay: .5s;
}
div.content > h1.slid {
  animation: slide-in .5s forwards;
}
.scale-enter-active {
  animation: scale-in .5s;
}
.scale-leave-active {
  animation: scale-in .5s reverse;
}
div#home {
  height: 100%;
  display: flex;
}
div#home > div.content {
  height: 100%;
  overflow: auto;
}
img.portrait {
  width: 10em;
  border-radius: 50%;
  margin: .5em;
}
div.content {
  display: inline-block;
  width: 100%;
}
div.content > * {
  margin-left: 1em;
  margin-right: 1em;
}
div.content > h1 {
  font-size: 5em;
  font-weight: normal;
  font-family: 'Montserrat', sans-serif;
  margin: 2em 1em 1em 1em;
  display: inline-block;
  color: #BF5700;
}
div.content > a {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 4em;
  font-family: 'Montserrat', sans-serif;
  margin: 0;
  color: #005F86;
  cursor: pointer;
}
div.content > a:hover {
  color: #005F86;
}
div.content ul.projects {
  list-style-type: none;
}
ul.projects > li > a {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-family: 'Montserrat', sans-serif;
  margin: 0;
  color: #69f;
  cursor: pointer;
  text-decoration: none;
}
ul.projects {
  padding: 0;
}
</style>



